export default {
	name:"SubMenu",
	props: {
		subItems: {
			type: Array,
			default(){
				return [];
			}
		},
	},
	computed: {
		orderByChildMenuList() {
			return this.sortByKey(this.subItems, "ShowOrder");
		},
	},
	data() {
		return {
			
		}
	},
	methods: {
		sortByKey(array, key) {
			return array.slice().sort(function(a, b) {
				var x = Number(a[key]);
				var y = Number(b[key]);
				return x < y ? -1 : x > y ? 1 : 0;
			});
		}
	}
}
